.about {
    width: 100%;
    color: $Primary_Text;
    text-align: center;
    @extend .center-f;
    background: url(./../imgs/About_Background_Mobile.jpg) no-repeat center;
    background-size: cover;
    background-attachment: fixed;
    position: relative;
    @media (min-width: 62rem) {
        & {
            background: url(./../imgs/About_Background_Desktop.jpg) no-repeat center;
            background-size: cover;
            background-attachment: fixed;
        }
    }
    @media (min-width: 75rem) {
        & {
            padding: 4rem 0;
        }
    }
    &::after {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: #0c0c0c9f;
        backdrop-filter: blur(2px);
        z-index: 1;
    }
    h2,
    .information .info,
    .information, .where {
        position: relative;
        z-index: 9;
    }
    .information {
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
        .info, .where {
            width: 18.75rem;
            margin: 1rem;
            @media (min-width: 62rem) {
                & {
                    margin: 1rem 3rem;
                }
            }
        }
        .info h4, .info p,
        .where h4, .where p {
            font-family: 'Bona Nova', serif;
        }
        .info p, .where p {
            font-size: 0.875rem;
            color: #d7d7d7;
            letter-spacing: 1px;
        }
        .info h4, .where h4 {
            font-size: 1.125rem;
            margin-bottom: .75rem;
            text-decoration: underline;
        }
    }
}