footer {
    margin-bottom: 4.375rem;
    @media (min-width: 48rem) {
        & {
            margin-bottom: unset;
        }
    }
    .container {
        width: 100%;
        background-color: $Secondary_Text;
        display: flex;
        flex-direction: column;
        align-items: center;
        text-align: center;
        padding: 1rem 0;
        @media (min-width: 48rem) {
            & {
                flex-direction: row;
                justify-content: space-around
            }
        }
        .social-media {
            a {
                img {
                    @extend .smoothPointer;
                    height: 2.5rem;
                }
                img:first-child {
                    margin-right: 1.25rem;
                }
                img:active {
                    transform: scale(.95);
                }
            }
        }
        .email p, .copyright p {
            color: $Primary_Text;
            font-family: "Bona Nova", serif;
        }
        .email {
            @media (min-width: 48rem) {
                & {
                    order: 2;
                }
            }
            p {
                margin: 1.25rem 0;
            }
        }
        .copyright {
            display: flex;
            @media (min-width: 48rem) {
                & {
                    order: 1;
                }
            }
            p {
                font-weight: 700;
                margin-right: .45rem;
            }
            span {
                font-family: 'Great Vibes', cursive;
                font-size: 1.2rem;
                text-transform: capitalize;
                color: #59c1c1;
            }
        }
    }
}