*, *::after, *::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Bebas Neue", cursive;
  scroll-behavior: smooth;
}

body {
  overflow-x: hidden;
  background-color: #090E0E;
}

ul, li {
  list-style: none;
}

a {
  text-decoration: none;
}

button, input {
  background: transparent;
  border: none;
  outline: none;
}

.center-f, .gallery .slider-container, .about {
  display: flex;
  align-items: center;
}

.center-g, .gallery .image-container, header .container .header-img {
  display: grid;
  place-items: center;
}

.position-flow {
  position: absolute;
  top: 0;
  left: 0;
}

.smoothPointer, footer .container .social-media a img, .gallery .slider-container .left-arrow,
.gallery .slider-container .right-arrow {
  transition: 0.5s;
  cursor: pointer;
}

.container {
