/* ------------------------- End style Container ------------------------- */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

* {
  scrollbar-color: var(--drb-Main-Color) transparent;
  scrollbar-width: thin;
}

::-webkit-scrollbar {
  width: 0.4em;
}

::-webkit-scrollbar-track {
  background: transparent;
}

::-webkit-scrollbar-thumb {
  background: var(--drb-Main-Color);
  border: 3px solid transparent;
  border-radius: 5px 0 0 5px;
}

.mobile-nav {
  padding: 1.5rem 1.875rem;
}
.mobile-nav .logo img {
  height: 2.25rem;
  filter: brightness(50);
}
.mobile-nav .bottom-nav {
  background-color: rgba(255, 255, 255, 0.0470588235);
  width: 100%;
  position: fixed;
  bottom: 0;
  left: 0;
  border-radius: 0.5rem 0.5rem 0 0;
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
  z-index: 9;
}
.mobile-nav .bottom-nav ul {
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 0.65rem 1.5rem;
}
.mobile-nav .bottom-nav ul li {
  width: 2.5rem;
}
.mobile-nav .bottom-nav ul li a {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.mobile-nav .bottom-nav ul li a img {
  width: 2rem;
  transform: translateY(0.625rem) scale(0.85);
  filter: saturate(0);
  transition: 0.5s;
}
.mobile-nav .bottom-nav ul li a p {
  margin-top: 0.188rem;
  font-size: 0.7rem;
  font-weight: 500;
  color: #38796b;
  white-space: nowrap;
  transform: scale(0);
  line-height: 0.938rem;
  font-family: "Changa", sans-serif;
  transition: 0.5s;
}
.mobile-nav .bottom-nav ul li.active img {
  filter: unset;
  transform: translateY(0rem) scale(1);
}
.mobile-nav .bottom-nav ul li.active p {
  transform: scale(1);
  display: inline-block;
}

.desktop-nav .container {
  display: flex;
  padding: 1.25rem 0;
}
.desktop-nav .container img {
  height: 2.813rem;
  margin-right: 6rem;
}
.desktop-nav .container ul {
  width: 50%;
  display: flex;
  align-items: center;
}
@media (min-width: 62rem) {
  .desktop-nav .container ul {
    margin-left: auto;
  }
}
.desktop-nav .container ul li {
  margin: 0 1.25rem;
  transition: 0.5s;
}
@media (min-width: 62rem) {
  .desktop-nav .container ul li {
    margin: 0 0.5rem;
  }
}
@media (min-width: 75rem) {
  .desktop-nav .container ul li {
    margin: 0 1.5rem;
  }
}
.desktop-nav .container ul li a {
  color: #FFFFFF;
  font-size: 0.875rem;
  white-space: nowrap;
  font-family: "Changa", sans-serif;
  position: relative;
}
.desktop-nav .container ul li a::after {
  content: "";
  position: absolute;
  bottom: 0.313rem;
  left: 0;
  height: 0.125rem;
  width: 0;
  background-color: #41c6c6;
  transition: 0.5s;
}
.desktop-nav .container ul li:hover:not(li:last-child) a::after {
  width: 100%;
}
.desktop-nav .container ul li:last-child {
  margin-left: 2.4rem;
}
.desktop-nav .container ul li:last-child a {
  color: #000000;
  background-color: #FFFFFF;
  padding: 0.45rem 1.65rem;
  border-radius: 0.313rem;
  transition: 0.5s;
}
.desktop-nav .container ul li:last-child a:hover {
  background-color: transparent;
  color: #FFFFFF;
  outline: 1px solid #FFFFFF;
}
.desktop-nav .container ul li:first-child {
  margin-left: 1.5rem;
}

