.partners {
    .container {
        .img-container {
            display: flex;
            flex-wrap: wrap;
            justify-content: center;
            .img-wrapper {
                width: 9.375rem;
                height: 4.375rem;
                margin: 1rem;
                text-align: center;
                @media (min-width: 62rem) {
                    & {
                        width: 15.625rem;
                        height: 5.625rem;
                    }
                }
                img {
                    height: 100%;
                }
            }
        }
    }
}