.contact {
    .container {
        form {
            width: 100%;
            max-width: 28.125rem;
            display: flex;
            flex-direction: column;
            position: relative;
            left: 50%;
            transform: translateX(-50%);
            @media (min-width: 62rem) {
                & {
                    max-width: 40.625rem;
                }
            }
            .inputs {
                input {
                    height: 2.188rem;
                    @media (min-width: 62rem) {
                        & {
                            height: 2.813rem;
                        }
                    }
                }
                @media (min-width: 62rem) {
                    & {
                        display: flex;
                    }
                    & :first-child{
                        margin-right: .75rem;
                    }
                }
            }
            .inputs input, .message textarea {
                width: 100%;
                padding: .75rem;
                color: $Primary_Text;
                background-color: #0f1616;
                font-family: "Bona Nova", serif;
                margin-bottom: .5rem;
                border: none;
                outline: none;
                border-radius: 0.313rem;
                border-bottom: 1px solid $Primary_Text;
                @media (min-width: 62rem) {
                    & {
                        margin-bottom: 0.875rem;
                    }
                }
            }
            .inputs input::placeholder, .message textarea::placeholder {
                color: #c5c5c5;
            }
            button {
                align-self: center;
            }
        }
    }
}