*, *::after, *::before {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Bebas Neue', cursive;
    scroll-behavior: smooth;
}
body {
    overflow-x: hidden;
    background-color: $Background_Color;
}
ul, li {
    list-style: none;
}
a {
    text-decoration: none;
}
button, input {
    background: transparent;
    border: none;
    outline: none;
}

// FramWorks
.center-f {
    display: flex;
    align-items: center;
}
.center-g {
    display: grid;
    place-items: center;
}
.position-flow {
    position: absolute;
    top: 0;
    left: 0;
}
.smoothPointer {
    transition: .5s;
    cursor: pointer;
}

.container {
    padding: 0 15px;
    margin: 0 auto;
}

.title {
    color: #fff;
    text-align: center;
    margin-bottom: 3rem;
    h2 {
        font-size: 2.125rem;
        margin-bottom: 0.5rem;
        letter-spacing: 2px;
        @media (min-width: 75rem) {
            & {
                font-size: 2.75rem;
                margin-bottom: 0.625rem;
            }
        }
    }
    p {
        font-family: 'Bona Nova', serif;
        margin-inline: 1rem;
    }
}

section > div {
    padding-blocK: 2.5rem;
}

/* ------------------------- Start style Container ------------------------- */
.container {
    padding: 0 15px;
    margin: 0 auto;
}
  /* Small */
@media (min-width: 48rem) {
    .container {
        width: 750px;
    }
}
  /* Medium */
@media (min-width: 62rem) {
    .container {
        width: 970px;
    }
}
  /* Large */
@media (min-width: 75rem) {
    .container {
        width: 1170px;
    }
}
  /* ------------------------- End style Container ------------------------- */

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

* {
    scrollbar-color: var(--drb-Main-Color) transparent;
    scrollbar-width: thin;
}
::-webkit-scrollbar {
    width: .4em;
}
::-webkit-scrollbar-track {
    background: transparent;
}
::-webkit-scrollbar-thumb {
    background: var(--drb-Main-Color);
    border: 3px solid transparent;
    border-radius: 5px 0 0 5px;
}