.gallery {
    color: $Primary_Text;
    overflow: hidden;
    @media (min-width: 48rem) {
        & {
            padding: 6rem 0;
        }
    }
    .slider-container {
        @extend .center-f;
        position: relative;
        width: 100%;
        min-height: 200px;
        justify-content: center;
        transition: .5s;
        @media (min-width: 48rem) {
            & {
                min-height: 300px;
            }
        }
        @media (min-width: 62rem) {
            & {
                min-height: 450px;
            }
        }
        .slider {
            position: absolute;
            display: flex;
            transition: 1s;
            .img {
                width: 9.375rem;
                height: 6.25rem;
                margin: 0 0.75rem;
                opacity: .6;
                background-repeat: no-repeat;
                background-size: cover;
                transition: .5s;
                @media (min-width: 48rem) {
                    & {
                        width: 17.188rem;
                        height: 12.5rem;
                        margin: 0 1.125rem;
                    }
                }
                @media (min-width: 62rem) {
                    & {
                        width: 22.5rem;
                        height: 15.625rem;
                    }
                }
            }
            .focus {
                transform: scale(1.35);
                margin: 0 2.75rem;
                opacity: 1;
                cursor: pointer;
                &:active {
                    transform: scale(1.25);
                }
                @media (min-width: 48rem) {
                    & {
                        margin: 0 4.375rem;
                    }
                }
                @media (min-width: 62rem) {
                    & {
                        margin: 0 7.5rem;
                    }
                }
            }
        }
        .top-corner,
        .bottom-corner {
            height: 2.188rem;
            width: 2.188rem;
            border: 0.188rem solid;
            position: absolute;
            left: 50%;
            @media (min-width: 48rem) {
                & {
                    height: 2.813rem;
                    width: 2.813rem;
                }
            }
            @media (min-width: 62rem) {
                & {
                    height: 3.75rem;
                    width: 3.75rem;
                }
            }
        }
        .top-corner {
            top: -0.938rem;
            transform: translate(-8.125rem);
            border-color: #1a9999 transparent transparent #1a9999;
            @media (min-width: 48rem) {
                & {
                    top: -2.188rem;
                    transform: translate(-13.75rem);
                }
            }
            @media (min-width: 62rem) {
                & {
                    top: -0.938rem;
                    transform: translate(-18.438rem);
                }
            }
        }
        .bottom-corner {
            top: 11.25rem;
            transform: translate(5.938rem);
            border-color: transparent #1a9999 #1a9999 transparent;
            @media (min-width: 48rem) {
                & {
                    top: 18.125rem;
                    transform: translate(11.25rem);
                }
            }
            @media (min-width: 62rem) {
                & {
                    top: 24.688rem;
                    transform: translate(15rem);
                }
            }
        }
        .left-arrow,
        .right-arrow {
            @extend .smoothPointer;
            height: 1.25rem;
            width: 1.25rem;
            border: 0.188rem solid;
            position: absolute;
            opacity: .5;
            @media (min-width: 62rem) {
                & {
                    height: 1.563rem;
                    width: 1.563rem;
                    border: 0.25rem solid;
                }
            }
        }
        .left-arrow:hover,
        .right-arrow:hover {
            opacity: 1;
        }
        .left-arrow {
            transform: translateX(1.25rem) translateY(-0.313rem) rotate(45deg);
            border-color: transparent transparent $Primary_Text $Primary_Text;
            left: 0;
        }
        .right-arrow {
            transform: translateX(-1.25rem) translateY(-0.313rem) rotate(45deg);
            border-color: $Primary_Text $Primary_Text transparent transparent;
            right: 0;
        }
    }
    .image-container {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: #000000e8;
        @extend .center-g;
        z-index: 9;
        display: none;
        .close-btn {
            position: fixed;
            top: 1rem;
            right: 1rem;
            img {
                height: 2rem;
                cursor: pointer;
                filter: invert(1);
            }
        }
        .focused-img {
            width: 90%;
            height: 80%;
            background-size: contain;
            background-position: center;
            background-repeat: no-repeat;
            opacity: 0;
            transform: translateY(-2rem);
            transition: 1s;
        }
    }
}