.mobile-nav {
    padding: 1.5rem 1.875rem;
    .logo {
        img {
            height: 2.25rem;
            filter: brightness(50);
        }
    }
    .bottom-nav {
        background-color: #ffffff0c;
        width: 100%;
        position: fixed;
        bottom: 0;
        left: 0;
        border-radius: 0.5rem 0.5rem 0 0;
        backdrop-filter: blur(10px);
        z-index: 9;
        ul {
            display: flex;
            justify-content: space-around;
            align-items: center;
            padding: .65rem 1.5rem;
            li {
                width: 2.5rem;
                a {
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    img {
                        width: 2rem;
                        transform: translateY(0.625rem) scale(.85);
                        filter: saturate(0);
                        transition: .5s;
                    }
                    p {
                        margin-top: 0.188rem;
                        font-size: 0.7rem;
                        font-weight: 500;
                        color: #38796b;
                        white-space: nowrap;
                        transform: scale(0);
                        line-height: 0.938rem;
                        font-family: 'Changa', sans-serif;
                        transition: .5s;
                    }
                }
            }
            li.active {
                img {
                    filter: unset;
                    transform: translateY(0rem) scale(1);
                }
                p {
                    transform: scale(1);
                    display: inline-block;
                }
            }
        }
    }
}
